import Router from "vue-router"
import VueResource from 'vue-resource'
import Vue from 'vue'
import App from '../module/dms/App'
import page1 from '../module/dms/views/page1'
import page2 from '../module/dms/views/page2'
import login from '../module/dms/views/login'
Vue.use(Router)
Vue.config.debug = true
Vue.use(VueResource)
const router = new Router({
  mode: 'hash',
  base: __dirname,
  routes: [
    {
      path: '/dms/login',
      component: login,
      name:'login'
    },
    {
      path: '/dms/page1',
      component: page1,
      name:'page1'
    },
    {
      path: '/dms/page2',
      component: page2,
      name:'page2'
    }
  ]
})
export default router



